//  vertical nav scss
.vertical-nav {
     position: absolute;
     top: 0;
     left: 0;
     height: 100%;

     .vertical-nav-inner {
          width: 100%;
          max-width: 60px;
          padding: 19px;
          border-right: 1px solid #606E7E;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;

          .nav-flex-section {
               display: flex;
               align-items: center;
               gap: 50px;
               transform: rotate(-90deg);
               -webkit-transform: rotate(-90deg);
               -moz-transform: rotate(-90deg);
               -ms-transform: rotate(-90deg);
               -o-transform: rotate(-90deg);
               height: 100%;
               position: relative;
               z-index: 30;

               .elem {

                    p {
                         color: #FFF;
                         font-family: Fira Sans;
                         font-size: 18px;
                         font-style: normal;
                         font-weight: 500;
                         line-height: normal;
                         cursor: pointer;
                         transition: .2s linear;
                         -webkit-transition: .2s linear;
                         -moz-transition: .2s linear;
                         -ms-transition: .2s linear;
                         -o-transition: .2s linear;

                    }

                    .facebook:hover {
                         color: #3b5998;

                    }

                    .instagram:hover {
                         background: #f09433;
                         background: -moz-linear-gradient(45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%);
                         background: -webkit-linear-gradient(45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%);
                         background: linear-gradient(45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%);
                         filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#f09433', endColorstr='#bc1888', GradientType=1);
                         color: transparent;
                         -webkit-background-clip: text;
                         background-clip: text;

                    }

                    .linkedin:hover {
                         color: #0A66C2;

                    }
               }
          }
     }

}

// -------------------- hero section ---------------------
// hero skull scss
.hero-section {
     width: 100%;
     max-width: 100%;
     padding: 137px 0 0 0;
     position: relative;

     .hero-section-inner {
          width: calc(100% - 210px);
          margin-left: auto;
          max-width: 100%;
          max-width: 1680px;

          @media only screen and (min-width:1500px) {
               width: calc(100% - 238px);
          }

          // overflow: hidden;

          .hero-card {
               width: 100%;
               max-width: 100%;

               .hero-card-inner {
                    display: flex;
                    justify-content: space-between;
                    align-items: stretch;
                    gap: 80px;
                    width: 100%;
                    margin-left: auto;

                    .text-sec {
                         width: 45%;
                         display: flex;
                         flex-direction: column;
                         gap: 30px;

                         h1 {
                              color: #FFF;
                              font-family: Fira Sans;
                              font-size: 50px;
                              font-style: normal;
                              font-weight: 700;
                              line-height: normal;

                              @media only screen and (min-width:1340px) {
                                   font-size: 56px;
                              }

                              @media only screen and (min-width:1520px) {
                                   font-size: 65px;
                              }

                              @media only screen and (min-width:1600px) {
                                   font-size: 75px;
                              }

                              @media only screen and (min-width:1800px) {
                                   font-size: 85px;
                              }

                         }

                         p {
                              color: #FFF;
                              font-family: Fira Sans;
                              font-size: 18px;
                              font-style: normal;
                              font-weight: 300;
                              line-height: 28px;
                              width: 100%;
                              max-width: 665px;

                              @media only screen and (min-width:1500px) {
                                   font-size: 20px;
                              }
                         }

                         .hero-btn {
                              padding-bottom: 80px;

                              @media only screen and (min-width:1600px) {
                                   padding-bottom: 40px;
                              }

                              @media only screen and (min-width:1800px) {
                                   padding-bottom: 00px;
                              }
                         }


                    }

                    .img-sec {
                         width: 55%;

                         border-radius: 65px 0 0 0;

                         img {
                              width: 100%;
                              height: 100%;
                         }


                    }
               }
          }

          #append-dots {
               background-color: #FFF !important;
               width: 100% !important;
               max-width: 140px !important;
               height: 2px !important;
          }
     }

     .slick-slider {
          height: 100% !important;

          .slick-list {
               height: 100% !important;

               .slick-track {
                    height: 100% !important;
               }

               .slick-track div {
                    height: 100% !important;
                    width: calc(100% - 10px);
                    margin: auto;
               }

          }
     }

     .slick-dots {
          transform: rotate(0deg) translateX(0%);
          -webkit-transform: rotate(0deg) translateX(0%);
          -moz-transform: rotate(0deg) translateX(0%);
          -ms-transform: rotate(0deg) translateX(0%);
          -o-transform: rotate(0deg) translateX(0%);
          left: -78%;
          bottom: 0px;
          padding: 0;
          max-width: 264px;
          z-index: 5;

          @media only screen and (min-width:1700px) {
               bottom: 70px;
          }
          @media only screen and (min-width:1870px) {
               bottom: 100px;
          }
          

          ul {
               height: 2px;
               display: flex;
               align-items: center;
               justify-content: center;
               gap: 2px;
               position: relative;
               cursor: pointer;

               &::before {
                    content: '01';
                    position: absolute;
                    color: #FFF;
                    font-family: Fira Sans;
                    font-size: 32px;
                    font-style: normal;
                    font-weight: 700;
                    line-height: normal;
                    top: -15px;
                    left: -20px;
                    transform: rotate(0deg);
                    -webkit-transform: rotate(0deg);
                    -moz-transform: rotate(0deg);
                    -ms-transform: rotate(0deg);
                    -o-transform: rotate(0deg);
               }

               &::after {
                    content: '03';
                    position: absolute;
                    color: #FFF;
                    font-family: Fira Sans;
                    font-size: 32px;
                    font-style: normal;
                    font-weight: 700;
                    line-height: normal;
                    top: -15px;
                    right: -40px;
                    transform: rotate(0deg);
                    -webkit-transform: rotate(0deg);
                    -moz-transform: rotate(0deg);
                    -ms-transform: rotate(0deg);
                    -o-transform: rotate(0deg);
               }

               li {
                    height: 2px;
                    width: 100%;
                    max-width: 88px;
                    background-color: #FFF;

                    button {
                         height: 2px;
                         color: #FFF;
                         height: 2px;
                         width: 100%;
                         max-width: 88px;

                         &::before {
                              display: none;
                         }
                    }

                    cursor: pointer;
               }

               .slick-active {
                    button {
                         color: #003ADB;
                    }

                    background-color: #003ADB;
               }
          }
     }
}

.hero-gradiant {
     display: none;

     @media only screen and (min-width:1200px) {
          display: block;
          background: linear-gradient(180deg, rgba(12, 17, 23, 0.00) 0%, #0C1117 77.08%);
          width: 100%;
          height: 100%;
          max-height: 260px;
          position: absolute;
          bottom: -5px;
     }
}


