          .slick-dots {
               transform: rotate(0deg) translateX(0%);
               -webkit-transform: rotate(0deg) translateX(0%);
               -moz-transform: rotate(0deg) translateX(0%);
               -ms-transform: rotate(0deg) translateX(0%);
               -o-transform: rotate(0deg) translateX(0%);
               left: -78%;
               top: 95%;
               padding: 0;
               max-width: 264px;
               z-index: 5;

               ul {
                    height: 2px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    gap: 2px;
                    position: relative;
                    cursor: pointer;

                    &::before {
                         content: '01';
                         position: absolute;
                         color: #FFF;
                         font-family: Fira Sans;
                         font-size: 32px;
                         font-style: normal;
                         font-weight: 700;
                         line-height: normal;
                         top: -15px;
                         left: -20px;
                         transform: rotate(0deg);
                         -webkit-transform: rotate(0deg);
                         -moz-transform: rotate(0deg);
                         -ms-transform: rotate(0deg);
                         -o-transform: rotate(0deg);
                    }

                    &::after {
                         content: '03';
                         position: absolute;
                         color: #FFF;
                         font-family: Fira Sans;
                         font-size: 32px;
                         font-style: normal;
                         font-weight: 700;
                         line-height: normal;
                         top: -15px;
                         right: -40px;
                         transform: rotate(0deg);
                         -webkit-transform: rotate(0deg);
                         -moz-transform: rotate(0deg);
                         -ms-transform: rotate(0deg);
                         -o-transform: rotate(0deg);
                    }

                    li {
                         height: 2px;
                         width: 100%;
                         max-width: 88px;
                         background-color: #FFF;

                         button {
                              height: 2px;
                              color: #FFF;
                              height: 2px;
                              width: 100%;
                              max-width: 88px;

                              &::before {
                                   display: none;
                              }
                         }

                         cursor: pointer;
                    }

                    .slick-active {
                         button {
                              color: #003ADB;
                         }

                         background-color: #003ADB;
                    }
               }
          }
     }

