.hero-section .slick-dots ul {
  height: 2px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2px;
  position: relative;
  cursor: pointer;
}
.hero-section .slick-dots ul::before {
  content: "01";
  position: absolute;
  color: #FFF;
  font-family: Fira Sans;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  top: -15px;
  left: -20px;
  transform: rotate(0deg);
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -ms-transform: rotate(0deg);
  -o-transform: rotate(0deg);
}
.hero-section .slick-dots ul::after {
  content: "03";
  position: absolute;
  color: #FFF;
  font-family: Fira Sans;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  top: -15px;
  right: -40px;
  transform: rotate(0deg);
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -ms-transform: rotate(0deg);
  -o-transform: rotate(0deg);
}
.hero-section .slick-dots ul li {
  height: 2px;
  width: 100%;
  max-width: 88px;
  background-color: #FFF;
