}
.hero-section .slick-dots ul li button {
  height: 2px;
  color: #FFF;
  height: 2px;
  width: 100%;
  max-width: 88px;
}
.hero-section .slick-dots ul li button::before {
  display: none;
}
.hero-section .slick-dots ul .slick-active {
